import { URL_SERVICIOS } from './../../config/url.servicios';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AlertController, Platform, ModalController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UsuarioProvider } from '../usuario/usuario';
import { LoginPage, CarritoPage } from '../../pages/index.paginas';



@Injectable()
export class CarritoProvider {

  items:any[] = [];
  total_carrito = 0;
  ordenes:any[] = [];

  constructor(public http: HttpClient,
         private alertCtrl: AlertController,
         private platform: Platform,
         private storage: Storage,
         private modalCtrl: ModalController,
         private _up: UsuarioProvider) {
          this.cargarStorage();
          this.actualizarTotal();
  }

  realizarPedido () {
    let codigos:string[]=[];
    for(let item of this.items) {
      codigos.push(item.codigo);
    }
    
    let params = { items: codigos.join(",") };
    // console.log(params);
    let url = URL_SERVICIOS + '/ordenes?token=' + this._up.token 
                            + '&usuario_id=' + this._up.id_usuario;
    this.http.post(url, params)
             .subscribe( (resp:any) => {
                 // recibo resp.orden
                 this.items = [];
                 if ( resp.error === true ) {
                     console.log(resp);
                     this.alertCtrl.create({
                        title: "Error al realizar la orden",
                        subTitle: "Avise al administrador.",
                        buttons: ["Ok"]
                     }).present();
                 } else {
                     this.alertCtrl.create({
                          title: "Orden realizada!",
                          subTitle: "Nos contactaremos con usted próximamente.",
                          buttons: ["Ok"]
                     }).present();
                }
             }, err => {
                    console.log(err);
                    this.alertCtrl.create({
                      title: "Error al realizar la orden",
                      subTitle: "Disculpe el inconveniente",
                      buttons: ["Ok"]
                    }).present();
             });
  }

  removerItem( idx: number) {
      this.items.splice(idx,1);
      this.guardarStorage();
  }


  public ver_carrito() {
       
       let modal:any;

      if(this._up.token && this._up.token.length>0) {
         // mostrar pagina de carrito
         modal = this.modalCtrl.create(CarritoPage);
      } else {
         // mostrar modal         
         modal = this.modalCtrl.create(LoginPage);
      }
      
      modal.present();
      modal.onDidDismiss( (abrirCarrito:boolean) => {
              if (abrirCarrito) {
                this.modalCtrl.create(CarritoPage).present();
              }
      });
  }

  agregarCarrito(item_parametro:any) {
     
     for(let item of this.items ) {
          if ( item.codigo == item_parametro.codigo ) {

            this.alertCtrl.create({
              title: "Item existe",
              subTitle: item_parametro.producto + ", ya se encuentra en su carrito de compras.",
              buttons: ["OK"]
            }).present();

            return;
          }

     }

     this.items.push(item_parametro);     
     this.guardarStorage();
     this.actualizarTotal();
     /*
     No olvidar plugin
      > cordova plugin add cordova-sqlite-storage --save
      > npm install --save @ionic/storage
     */
  }

  actualizarTotal() {
    this.total_carrito = 0;
    for(let item of this.items) {
      this.total_carrito += Number(item.precio_compra);
    }
  }

  guardarStorage() {
      if ( this.platform.is("cordova") ) {
        // dispositivo
        this.storage.set('items', this.items);
      } else {
        // compu
        localStorage.setItem('items', JSON.stringify(this.items));
      }
    
  }

  cargarStorage() {

      let promesa = new Promise((resolve, reject)=> {
              if ( this.platform.is("cordova") ) {

                    this.storage.ready().then( () => {
                          this.storage.get("items")
                                      .then( items => {
                                    if ( items ) {
                                      this.items = items;
                                    }
                                    resolve();
                              });
                    });
                   
              } else {
                  // compu
                  if (  localStorage.getItem("items") ) {
                    this.items = JSON.parse( localStorage.getItem("items"));                    
                  }

                  resolve();
              }
      });
      
      
      return promesa;     
  }

  cargarOrdenes() {
    let url = URL_SERVICIOS + '/ordenes?id=' + this._up.id_usuario;
    this.http.get(url).subscribe( (resp:any) => {
                        // recibo resp.orden
                        this.items = [];
                        if ( resp.error === true ) {
                            console.log(resp);
                            this.alertCtrl.create({
                               title: "Error al cargar ordenes",
                               subTitle: "Avise al administrador.",
                               buttons: ["Ok"]
                            }).present();
                        } else {
                            
                          this.ordenes = resp.ordenes;
                           
                       }
                    }, err => {
                           console.log(err);
                           this.alertCtrl.create({
                             title: "Error al cargar ordenes",
                             subTitle: "Disculpe el inconveniente",
                             buttons: ["Ok"]
                           }).present();
                    });
  }

  borrarOrden(orden_id: string) {
    let url = URL_SERVICIOS + '/ordenes/'+ orden_id  +'?token=' + this._up.token;
    return this.http.delete(url).map( resp => resp);
  }

}



