import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { PorCategoriasPage,
          OrdenesDetallePage,
          HomePage,
          CarritoPage,
          CategoriasPage,
          LoginPage,
          OrdenesPage,
          ProductosPage,
          TabsPage, 
          BusquedaPage} 
    from '../pages/index.paginas';
import { CarritoProvider,
         ProductosProvider, 
         UsuarioProvider  
    } from '../providers/index.providers';
import { HttpClientModule } from '@angular/common/http';
import { PipesModule } from '../pipes/pipes.module';



@NgModule({
  declarations: [
    MyApp, 
    PorCategoriasPage,
    OrdenesDetallePage,
    HomePage,
    CarritoPage,
    CategoriasPage,
    LoginPage,
    OrdenesPage,
    ProductosPage,
    TabsPage,
    BusquedaPage
  ],
  imports: [
    BrowserModule, HttpClientModule, PipesModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    PorCategoriasPage,
    OrdenesDetallePage,
    HomePage,
    CarritoPage,
    CategoriasPage,
    LoginPage,
    OrdenesPage,
    ProductosPage,
    TabsPage,
    BusquedaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    CarritoProvider,
    ProductosProvider,
    UsuarioProvider
  ]
})
export class AppModule {}



