import { ProductosPage } from './../index.paginas';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProductosProvider } from '../../providers/index.providers';

@IonicPage()
@Component({
  selector: 'page-busqueda',
  templateUrl: 'busqueda.html',
})
export class BusquedaPage {
  
  items: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
              public _productoProvider: ProductosProvider) {
  }  

  buscarProductos(ev: any) {

    // set val to the value of the searchbar
    const val = ev.target.value;
    
    this._productoProvider.buscar_producto(val);
    
  }

  irAPaginaProducto(item: any) {
    this.navCtrl.push(ProductosPage, { 'producto': item } );
  } 

}



