import { AlertController, Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { map, catchError } from 'rxjs/operators';
import { URL_SERVICIOS } from './../../config/url.servicios';
import { HttpClient, HttpErrorResponse  } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the UsuarioProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UsuarioProvider {

  token:string ="";
  id_usuario:string = "";
    
  constructor( private alertController: AlertController,
               public http: HttpClient,
               private platform: Platform,
               private storage: Storage,) {
        this.cargarStorage();
  }

  public activo () : boolean {
      return !(this.token==="");
  }

  ingresar( correo:string, contrasena:string ) {
            
      let url = URL_SERVICIOS + "/login";
      
      return this.http.post<any>(url, { correo: correo, contrasena: contrasena })
            .pipe(map( (resp) => {
                //console.log(resp);              
                let datos = resp; //.json();
                this.token = datos.token;
                this.id_usuario  = datos.login._id;
                // Guardar storage   
                // console.log(datos.login);
                this.guardarStorage();
                return datos.login;
            }), catchError( ( error: HttpErrorResponse ) => {
                    console.log(error);
                    if ( error.error.error  ) {
                       //console.log("Entrando a if de error");
                       //console.log(error.error);
                       this.alertController.create({
                            title: "Error al iniciar",
                            subTitle: error.error.mensaje || "Server Error",
                            buttons: ["Ok"]
                          }).present();
                    } 
                  // maseflores79@gmail.com
                  var obsArray = [];
                  obsArray.push( JSON.parse('{"error": false,"login":{"_id": "-","correo": "-","contrasena": "=)","token": "-"}}') );  
                  return obsArray;
            }) 
           );
  } 
  
  cerrarSesion() {
    this.token = null;
    this.id_usuario = null;
    // guardar storage de nuevo
    this.guardarStorage();
  }

  guardarStorage() {
    if ( this.platform.is("cordova") ) {
      // dispositivo
      this.storage.set('token', this.token);
      this.storage.set('id_usuario', this.id_usuario);
    } else {
      // compu      
      if ( this.token ) {
          localStorage.setItem('token', this.token);
          localStorage.setItem('id_usuario', this.id_usuario);
      } else {
          localStorage.removeItem('token');
          localStorage.removeItem('id_usuario');
          this.token = "";
          this.id_usuario = "";
      }
    }
  
  }
  
  cargarStorage() {
  
      let promesa = new Promise((resolve, reject)=> {
              if ( this.platform.is("cordova") ) {
  
                    this.storage.ready().then( () => {
                          this.storage.get("token")
                                      .then( token => {
                                    if ( token ) {
                                      this.token = token;
                                    }
                                    resolve();
                              });
                          this.storage.get("id_usuario")
                                      .then( id_usuario => {
                                    if ( id_usuario ) {
                                      this.id_usuario = id_usuario;
                                    }
                                    resolve();
                              });
                    });
                   
              } else {
                  // compu
                  if (  localStorage.getItem("token") ) {
                    this.token = localStorage.getItem("token");
                    this.id_usuario = localStorage.getItem("id_usuario");
                  }  
                  resolve();
              }
      });
      
      
      return promesa;     
  }

}



