
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ProductosProvider, CarritoProvider } from '../../providers/index.providers';
import { ProductosPage } from '../index.paginas';
import { UsuarioProvider } from '../../providers/usuario/usuario';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,
              public _productoProvider: ProductosProvider, 
              public _carritoProvider: CarritoProvider,
              public _usuarioProvider: UsuarioProvider) {  
  }
  
  siguientePagina(infiniteScroll) {    
   // console.log('Begin async operation');
    this._productoProvider.cargarTodos()
                          .then( () => {infiniteScroll.complete();} );    
  }

  irAPaginaProducto(item: any) {
    this.navCtrl.push(ProductosPage, { 'producto': item } );
  } 
  
}



