import { AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { URL_SERVICIOS } from '../../config/url.servicios';
import { Observable } from 'rxjs/Rx';

/*
  Generated class for the ProductosProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ProductosProvider {

  pagina = 0;
  productos:any[] = [];
  lineas:any[] = [];
  porCategoria:any[] = [];

  resultados:any[] = [];
  
  /* Para muestra de detalles de ordenes */
  productoDetalle: any[] = [];
  totalOrdenDetalle = 0 ;

  constructor(private alertController: AlertController,
              public http: HttpClient) {
    this.cargarTodos();
    this.cargarLineas();
  }

  cargarLineas() {
      let promesa = new Promise( (resolve, reject) => {
      let url = URL_SERVICIOS + '/lineas';    
   
      this.http.get(url).subscribe(  (data:any) => { 
                              
                              if (data.error) {
                                // problema
                              } else {                                
                                this.lineas = data.lineas;
                              }
                              resolve();
                         });
        });

        return promesa;
  }

  cargarPorCategoria(categoria: string) {
    let promesa = new Promise( (resolve, reject) => {
      let url = URL_SERVICIOS + '/productos?tipo='+categoria;
   
      this.http.get(url)
               .subscribe(  (data:any) => { 
                      // console.log('De subscribe ',  data)
                      if (data.error) {
                        // problema
                              this.alertController.create({
                                title: "Error al cargar por categoría",
                                subTitle: "Probablemente el servidor esté fuera de servicio.",
                                buttons: ["Ok"]
                              }).present();
                      } else {
                       // let nuevaData = this.agrupar(data.productos, 2);
                        this.porCategoria = data.productos ;
                      }
                      resolve();
                       });
      });
      
      return promesa;
  }

  cargarTodos() {
    let promesa = new Promise( (resolve, reject) => {
          let url = URL_SERVICIOS + '/productos?pagina=' + this.pagina;    
       
          this.http.get(url)
                   /*.map( (resp:any) => {
                       console.log('De map ', resp);
                       return resp.productos;
                      } ) */
                   .subscribe(  (data:any) => { 
                          // console.log('De subscribe ',  data)
                          if (data.error) {
                            // problema
                          } else {
                            // agregar a arreglo de productos
                            let nuevaData = this.agrupar(data.productos, 2);
                            this.productos.push(...nuevaData);
                            /*  el operador de tres puntos me ayuda a meter cada elemento del array
                                de data.productos dentro de this.productos, porque de lo contrario,
                                se meteria el array mismo de data.productos como 1 solo elemento del
                                array this.productos. Got it?
                            */
                           this.pagina = this.pagina + 1;
                          }
                          resolve();
                     });
    });

    return promesa;
  }

  cargarProductosDetalle(ordenDetalles) : Observable<any>  {

    let observable = new Observable( (observer) => {     
         //  console.log(ordenDetalles);
          this.productoDetalle = [];
          this.totalOrdenDetalle = 0 ;
        
          for(let detalle of ordenDetalles) {      
            let codigo = detalle.producto_id;      
            this.cargarProducto(codigo).then( () => { 
                        observer.next( this.productoDetalle );              
                    });
          }          
        })
        .map( (resp:any) => console.log(resp) );
        return observable;
  }

  cargarProducto(codigo: string) {
    let promesa = new Promise( (resolve, reject) => { 
          let url = URL_SERVICIOS + '/productos/codigo/' + codigo;    
   
          this.http.get<any>(url).subscribe(  (data:any) => { 
                              
                              if (data.error) {
                                  this.alertController.create({
                                    title: "Error al cargar detalle de producto",
                                    subTitle: "Probablemente el servidor esté fuera de servicio.",
                                    buttons: ["Ok"]
                                  }).present();
                              } else {
                                // console.log(data.producto);
                                this.totalOrdenDetalle += data.producto.precio_compra;
                                this.productoDetalle.push(data.producto);                      
                                return data.producto;
                              } 
                        
          });
      });
      return promesa;     
  }


  private agrupar(arr:any, tamano:number) {
      let nuevoArreglo = [];
      for ( let i=0; i<arr.length; i+= tamano ) {
         nuevoArreglo.push(arr.slice( i, i+tamano ) );
      }

     // console.log(nuevoArreglo);
      return nuevoArreglo;
  }

  buscar_producto(termino: string) {
    let url = URL_SERVICIOS + '/productos/buscar/' + termino;
    if ( termino.length > 0 ) { 
          this.http.get<any>(url).subscribe(  (data:any) => { 
                                    
                    if (data.error) {
                        this.alertController.create({
                          title: "Error al cargar detalle de producto",
                          subTitle: "Probablemente el servidor esté fuera de servicio.",
                          buttons: ["Ok"]
                        }).present();
                    } else {
                      //console.log(data.productos);
                      this.resultados = data.productos;           
                      return data.productos;
                    }        
              });
    } else {
      this.resultados = [];
      return this.resultados;
    }

  }
}



