import { Pipe, PipeTransform } from '@angular/core';
import { URL_IMAGENES } from '../../config/url.servicios';

@Pipe({
  name: 'imagen',
})
export class ImagenPipe implements PipeTransform {
    
  transform(value: string) {
    return URL_IMAGENES + value; // + ".jpg";
  }
}



