import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CarritoProvider } from '../../providers/carrito/carrito';
import { UsuarioProvider } from '../../providers/usuario/usuario';
import { OrdenesDetallePage } from '../ordenes-detalle/ordenes-detalle';

@IonicPage()
@Component({
  selector: 'page-ordenes',
  templateUrl: 'ordenes.html',
})
export class OrdenesPage {

  ordenesDetalle = OrdenesDetallePage;

  constructor(public navCtrl: NavController, public navParams: NavParams,
              public _carritoProvider: CarritoProvider,
              public _usuarioProvider: UsuarioProvider) {
  }

  ionViewWillEnter() {

    this._carritoProvider.cargarOrdenes();
  }
  

}



